import { TAnswerVariant } from '../typings/types/Question';
import shuffleArray from './shuffleArray';
import findRandomIndex from './findRandomIndex';

const getQuestionAnswerVariants = (variants: TAnswerVariant[], answer:string):TAnswerVariant[] => {
  const newVariants = shuffleArray<TAnswerVariant>(variants);
  const answerIndex:number = newVariants.findIndex((v) => v.id === answer);
  const answerObj:TAnswerVariant = newVariants.splice(answerIndex, 1)[0];
  const selectedVariants: TAnswerVariant[] = newVariants.splice(0, 3);
  selectedVariants.splice(findRandomIndex(3), 0, answerObj);
  return selectedVariants;
};

export default getQuestionAnswerVariants;
